import { useState } from "react";

import "./styles/index.scss";

import Name from "./components/Name";
import PrimaryButton from "./components/PrimaryButton";
import Projects from "./components/Projects";
import Nav from "./components/Nav";

import { ReactComponent as Education } from "./components/Icons/Education.svg";
import { ReactComponent as Skills } from "./components/Icons/Skills.svg";
import { ReactComponent as Reasearch } from "./components/Icons/Research.svg";
import { ReactComponent as Hobby } from "./components/Icons/Hobby.svg";
import { ReactComponent as Home } from "./components/Icons/house.svg";

import About from "./screens/about";
import Contact from "./screens/contact";
import HelperScreen from "./screens/helpers";

const App = () => {
  //const [currentScreen, setCurrentScreen] = useState("main");
  const [screenHistory, setScreenHistory] = useState(["main"]);
  const [backHistory, setBackHistory] = useState([]);
  const [lastAction, setLastAction] = useState(true);

  const addToScreenHistory = (screen) => {
    setScreenHistory([...screenHistory, screen]);
    setLastAction(true);
  };

  const pushFromScreenHistory = () => {
    setBackHistory([...backHistory, screenHistory[screenHistory.length - 1]]);
    setScreenHistory(
      screenHistory.filter((_, index) => index !== screenHistory.length - 1)
    );
    setLastAction(false);
  };

  const currentScreen = screenHistory[screenHistory.length - 1];
  const lastBackScreen = backHistory[backHistory.length - 1];

  return (
    <div className="App">
      <div
        className={`main-screen  ${
          currentScreen === "about" && "main-out-left"
        } 
        ${
          currentScreen == "project" && "overit"
        } 
        ${currentScreen === "contact" && "main-out-top"}
        ${
          currentScreen !== "main" &&
          currentScreen !== "about" &&
          currentScreen !== "contact" &&
          currentScreen !== "project" &&
          "main-out-doo"
        }  
        ${
          currentScreen === "main" &&
          currentScreen !== "about" &&
          currentScreen !== "contact" &&
          lastBackScreen !== "about" &&
          lastBackScreen !== "contact" &&
          lastBackScreen !== "project" &&
          lastBackScreen &&
          "main-in-doo"
        }  
        ${
          currentScreen === "main" &&
          lastBackScreen === "about" &&
          "main-in-right"
        }
        ${
          currentScreen === "main" &&
          lastBackScreen === "contact" &&
          "main-in-top"
        }

        `}
      >
        <div className="main-name-layout">
          <Name firstName="آژند" lastName="مردعلیزاد" />
        </div>
        <div className="main-nav-layout">
          <Nav addToScreenHistory={addToScreenHistory} />
        </div>
        <div className="buttons-col">
          <PrimaryButton color="primary" Icon={Home} />
          <PrimaryButton
            onClick={() => addToScreenHistory("education")}
            color="cream"
            Icon={Education}
          />
          <PrimaryButton
            onClick={() => addToScreenHistory("skills")}
            color="cream"
            Icon={Skills}
          />
          <PrimaryButton
            onClick={() => addToScreenHistory("research")}
            color="cream"
            Icon={Reasearch}
          />
          <PrimaryButton
            onClick={() => addToScreenHistory("hobby")}
            color="cream"
            Icon={Hobby}
          />
        </div>
        <div
          className={`main-projects-layout ${
            currentScreen == "project" && "expand-projects"
          } ${
            currentScreen == "main" &&
            lastBackScreen == "project" &&
            "shrink-projects"
          }`}
        >
          <Projects
            currentScreen={currentScreen}
            addToScreenHistory={addToScreenHistory}
            pushFromScreenHistory={pushFromScreenHistory}
            lastScreen={lastBackScreen}
          />
        </div>
      </div>
      <div
        className={`about-screen ${
          currentScreen === "about" ? "about-screen-in" : ""
        }  ${
          currentScreen === "main" &&
          lastBackScreen === "about" &&
          !lastAction &&
          "about-screen-out"
        }`}
      >
        <About pushFromScreenHistory={pushFromScreenHistory} />
      </div>
      <div
        className={`contact-screen ${
          currentScreen === "contact" ? "contact-screen-in" : ""
        }  ${
          currentScreen === "main" &&
          lastBackScreen === "contact" &&
          !lastAction &&
          "contact-screen-out"
        }`}
      >
        <Contact pushFromScreenHistory={pushFromScreenHistory} />
      </div>
      <div
        className={`helper-screen    ${
          currentScreen !== "main" &&
          currentScreen !== "about" &&
          currentScreen !== "contact" &&
          currentScreen !== "project" &&
          "helper-in-doo"
        } ${
          currentScreen === "main" &&
          lastBackScreen !== "about" &&
          lastBackScreen !== "contact" &&
          lastBackScreen !== "project" &&
          lastBackScreen &&
          "helper-out-doo"
        }  `}
      >
        <HelperScreen
          currentScreen={currentScreen}
          pushFromScreenHistory={pushFromScreenHistory}
        />
      </div>
    </div>
  );
};

export default App;
