import { useState, useEffect } from "react";
import { ReactComponent as Arrow } from "../../components/Icons/Arrow.svg";

import Education from "./Education";
import Hobby from "./Hobby";
import Skills from "./Skills";
import Research from "./Research";

const HelperScreen = ({ pushFromScreenHistory, currentScreen }) => {
  const [lastScreen, setLastScreen] = useState(null);

  useEffect(() => {
    if (currentScreen && currentScreen !== "main") {
      setLastScreen(currentScreen);
    }
  }, [currentScreen]);

  const renderProperScreen = () => {
    switch (lastScreen) {
      case "education":
        return <Education />;

      case "hobby":
        return <Hobby />;

      case "skills":
        return <Skills />;

      case "research":
        return <Research />;

      default:
        break;
    }
  };

  return (
    <div className="helper-screen-title">
      <div
        className="helper-back-button"
        onClick={() => pushFromScreenHistory()}
      >
        <Arrow />
      </div>
      <div>{renderProperScreen()}</div>
    </div>
  );
};

export default HelperScreen;
