import { useState } from "react";
import { ReactComponent as Arrow } from "../../components/Icons/Arrow.svg";
import TextField from "../../components/TextField";

const Contact = ({ pushFromScreenHistory }) => {
  const [answer, setAnswer] = useState("");

  const is42 = () => {
    if (answer === "42") return true;
    if (answer === 42) return true;
    if (answer === "۴۲") return true;
    return false;
  };

  return (
    <div>
      <div
        className="contact-back-button"
        onClick={() => pushFromScreenHistory()}
      >
        <Arrow />
      </div>
      <div className="contact-container">
        <div className="my-email">
          <a href="mailto:aj.mardalizad@gmail.com">
            ایمیل: aj.mardalizad@gmail.com
          </a>
        </div>
        <p className="social-description contact-para">
          من علاقه‌ای به تلف کردن وقتم تو شبکه‌های اجتماعی ندارم ولی اگه یه معما
          حل کنی می‌تونی شماره تلفن‌ام رو داشته باشی.
        </p>
        <div className="question">
          <TextField
            label="جواب زندگی، هستی و هرچیزی؟"
            value={answer}
            onChange={setAnswer}
          />
        </div>
        {answer && is42() && <p className="my-phone contact-para">بینگو!! ۰۹۰۵۳۹۵۲۸۸۳</p>}
        {answer && !is42() && <p className="noch contact-para">نچ!</p>}
      </div>
    </div>
  );
};

export default Contact;
